<template>
  <div>
    <div class="row">
      <div class="col-md-8">
        <router-view/>
      </div>
      <div class="col-md-4">
        <h4>最新笔记列表</h4>
        <hr>
        <router-link v-for="issue in issueList" :key="issue.id" class="nav-link" :to="'/'+issue.type+'/'+issue.id">{{issue.title}}</router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      issueList: []
    };
  },
  created() {
    const myList = this.GLOBAL;
    this.issueList = myList.filter(l => l.type === "issues").reverse();
  }
};
</script>

<style scoped>
.nav-link{
  color: #2c3e50;
}
</style>



